.section {
  padding-top: 121px;
  padding-bottom: 98px;
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
  .section {
    padding-top: 122px;
    padding-bottom: 112px;
  }
}
@media screen and (min-width: 1200px) {
  .section {
    padding-top: 127px;
    padding-bottom: 119px;
  }
}
.container {
  position: relative;
  // padding: 0 20px;
  margin: 0 auto;
}
@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 34px;
    padding-right: 34px;
    padding: 0 20px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.section-label {
  margin-bottom: 15px;
  font-family: 'Titan One', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 1.16;
  text-align: center;
  text-transform: uppercase;
  color: $main-shade-color;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .label-title {
    font-size: 26px;
    line-height: 1.15;
  }
}
.section-title {
  font-family: 'Titan One', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 58px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $main-color;
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
  .section .section-title {
    margin-bottom: 39px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .section-title {
    font-size: 48px;
    line-height: 1.14;
  }
}

@media screen and (min-width: 768px) {
  .products-list {
    display: flex;
    margin: -10px;
  }
}
.products-list__title {
  font-family: Titan One;
  font-weight: 400;
  font-size: 20px;
  font-style: normal;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-align: center;
  text-transform: uppercase;
  color: $primary-color;
}
.products-list__item {
  position: relative;
  padding-top: 113px;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .products-list__item {
    padding-top: 104px;
  }
}
@media screen and (min-width: 768px) {
  .products-list__item:before {
    height: 226px;
    width: 220px;
  }
}

@media screen and (min-width: 768px) {
  .products-list .products-list__item {
    margin: 10px;
    width: calc(33.33333% - 20px);
  }
}
.products-list__item:not(:last-child) {
  margin-bottom: 14px;
}
.products-list__item:before {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  display: block;
  content: '';
  width: 249px;
  height: 252px;
}
.products-list__item:first-child:before {
  background-image: url(/images/mobile/products/mobile-icecream1x.png);
}

.products-list__item:first-child .products-list__wrapp--card {
  background-color: $main-shade-two-color;
}

.products-list__wrapp--card {
  padding: 161px 40px 41px;
  border-radius: 24px;
}
@media screen and (min-width: 768px) {
  .products-list__wrapp--card {
    padding: 152px 11px 40px;
  }
}
.products-list__item:nth-child(2):before {
  background-image: url(/images/mobile/products/mobile-icecoffee1x.png);
}
.products-list__item:nth-child(2) .products-list__wrapp--card {
  background-color: $background-color;
}

.products-list__item:last-child:before {
  background-image: url(/images/mobile/products/mobile-milkshakes1x.png);
}
.products-list__item:last-child .products-list__wrapp--card {
  background-color: $background-two-color;
}
.products-list__text {
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  letter-spacing: 0.04em;
  color: $primary-color;
}
@media screen and (min-width: 768px) {
  .products-list__text {
    margin-bottom: 40px;
    line-height: 1.78;
  }
}
.products-list__text:before {
  display: block;
  content: '';
  margin: 30px auto;
  width: 26px;
  height: 6px;
  background-image: url(/images/svg/group-decore.svg);
}
.products-btn {
  cursor: pointer;
  width: 40px;
  height: 40px;
  border: 0;
  border-radius: 50%;
  fill: $main-color;
  background-color: $primary-color;
  outline: none;
}
